// Filters
.filters-title {
    text-align: center;
    margin-top: 0;
    font-weight: 400;
}

.filters {
    display: flex;
    justify-content: space-between;
}

.filter {
    padding: 5px 15px;
    border-radius: 7px 2px 2px 2px;
    font-weight: 700;
    font-size: 18px;
    min-width: 35px;
    width: 100%;
    text-align: center;
    opacity: 0.5;

    &.active {
        opacity: 1;
    }

    &:not(:nth-of-type(1)) {
        margin-left: 5px;
    }

    &:hover {
        cursor: pointer;
    }

    &.a1:hover {
        background: darken($a1, 10%)
    }

    &.a2:hover {
        background: darken($a2, 10%)
    }

    &.a3:hover {
        background: darken($a3, 10%)
    }

    &.a4:hover {
        background: darken($a4, 10%)
    }

    &.a5:hover {
        background: darken($a5, 10%)
    }

    &.a6:hover {
        background: darken($a6, 10%)
    }

    &.a7:hover {
        background: darken($a7, 10%)
    }

    &.a8:hover {
        background: darken($a8, 10%)
    }

    &.a9:hover {
