    }

    &.a12:hover {
        background: darken($a12, 10%)
    }
}

// Search
.search-bar {
    margin-top: 30px;
}

.search-bar__input {
    height: 26px;
    line-height: 26px;
    font-size: 20px;
}

// Results
.results {
    margin-top: 30px;
}

.result {
    align-items: flex-start;
    display: flex;
    margin-bottom: 10px;
}

.result-text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 10px;
}

.result-name {
    font-size: 20px;
    font-style: italic;
    font-weight: 300;
    margin-top: 0;
    margin-bottom: 5px;
    text-transform: lowercase;
}

.result-parent {
    font-weight: 600;
    margin-bottom: 5px;
}

.result-definition {
    font-size: 14px;
    margin-bottom: 0;
    margin-top: 0;
}

.result-location, .result-function, .result-action, .result-origin, .result-insertion {
    font-size: 14px;
    margin-top: 5px;
    margin-bottom: 0;

    span {
        font-weight: 700;
    }
}

.badge {
    font-weight: 700;
    font-size: 14px;
    padding: 5px;
    border-radius: 2px;
    height: 19px;
    white-space: nowrap;
}